import { FC, useCallback, useContext } from "react"
import { Controller, useForm, useFormState } from "react-hook-form"
import { useTranslation } from "react-i18next"
import {
  Button,
  CloseIcon,
  Input,
  Modal,
  TextArea,
  getColor,
  useMessage,
} from "@illa-design/react"
import { AppListContext } from "../../context"
import { AppSettingModalProps } from "./interface"
import {
  applyConfigItemLabelText,
  closeIconHotSpotStyle,
  formLabelStyle,
  gridFormFieldStyle,
  gridFormStyle,
  gridItemStyle,
  gridValidStyle,
  modalHeaderWrapperStyle,
  modalTitleStyle,
  modalWrapperStyle,
} from "./style"

export interface AppSettingFields {
  appName: string
  description?: string
}

export const AppSettingModal: FC<AppSettingModalProps> = (props) => {
  const {
    appID,
    appDesc = "",
    appName = "",
    onVisibleChange,
    onSaveEvent,
    onCloseEvent,
  } = props

  const { updateAppConfig } = useContext(AppListContext)

  const { control, formState, handleSubmit, reset } = useForm<AppSettingFields>(
    {
      mode: "onSubmit",
      criteriaMode: "firstError",
      defaultValues: {
        appName: appName,
        description: appDesc,
      },
    },
  )

  const { isSubmitting, isDirty } = useFormState({
    control,
  })

  const { t } = useTranslation()
  const message = useMessage()

  const onSubmit = useCallback(
    async (data: AppSettingFields) => {
      try {
        onSaveEvent()
        updateAppConfig(appID, {
          description: data.description,
          appName: data.appName,
        })

        reset({
          appName: data.appName,
          description: data.description,
        })
        message.success({
          content: t("dashboard.app.rename_success"),
        })
      } catch (error) {
        message.error({
          content: t("dashboard.app.rename_fail"),
        })
      }
      onVisibleChange(false)
    },
    [onVisibleChange, onSaveEvent, updateAppConfig, appID, reset, message, t],
  )

  return (
    <Modal
      maskClosable
      withoutPadding
      w="528px"
      visible
      footer={false}
      onCancel={() => {
        onCloseEvent()
        onVisibleChange(false)
      }}
    >
      <div css={modalWrapperStyle}>
        <header css={modalHeaderWrapperStyle}>
          <h3 css={modalTitleStyle}>
            {t("new_dashboard.app_setting.app_setting")}
          </h3>
          <span
            css={closeIconHotSpotStyle}
            onClick={() => {
              onCloseEvent()
              onVisibleChange(false)
            }}
          >
            <CloseIcon />
          </span>
        </header>
        <form
          css={gridFormStyle}
          autoComplete="off"
          onSubmit={handleSubmit(onSubmit)}
        >
          <section css={gridFormFieldStyle}>
            <section css={gridItemStyle}>
              <label css={formLabelStyle}>
                {t("new_dashboard.app_setting.app_name")}
                <span css={applyConfigItemLabelText(getColor("red", "02"))}>
                  *
                </span>
              </label>
              <div css={gridValidStyle}>
                <Controller
                  name="appName"
                  control={control}
                  render={({ field }) => (
                    <Input
                      {...field}
                      colorScheme="techPurple"
                      error={!!formState?.errors.appName}
                      placeholder={t(
                        "new_dashboard.app_setting.placeholder.app_name",
                      )}
                    />
                  )}
                  rules={{
                    required: t(
                      "page.user.sign_up.error_message.username.require",
                    ),
                    maxLength: {
                      value: 280,
                      message: t(
                        "page.user.sign_up.error_message.username.length",
                      ),
                    },
                  }}
                />
              </div>
            </section>
            <section css={gridItemStyle}>
              <label css={formLabelStyle}>
                {t("new_dashboard.app_setting.description")}
              </label>
              <div css={gridValidStyle}>
                <Controller
                  name="description"
                  control={control}
                  render={({ field }) => (
                    <TextArea
                      {...field}
                      showWordLimit
                      maxLength={180}
                      autoSize={{ minRows: 6, maxRows: 6 }}
                      colorScheme="techPurple"
                      error={!!formState?.errors.description}
                      placeholder={t(
                        "new_dashboard.app_setting.placeholder.description",
                      )}
                    />
                  )}
                />
              </div>
            </section>
          </section>
          <Button
            colorScheme="techPurple"
            loading={isSubmitting}
            disabled={!isDirty}
            fullWidth
          >
            {t("new_dashboard.app_setting.save")}
          </Button>
        </form>
      </div>
    </Modal>
  )
}

AppSettingModal.displayName = "AppSettingModal"
